.container-about {
  margin: auto;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  color: var(--white-smoke-color);
}

.about-left {
  display: flex;
  gap: 0.4rem;
  flex-direction: column;
  max-width: auto;
}

.left-skills {
  margin: 1rem 0;
}

.about-right img {
  border-radius: 100%;
  width: 100%;
  max-width: 180px;
  margin-bottom: 20px;
  height: auto;
}

.tech-stack {
  margin: 1rem;
}


.about-see {
  border-bottom: 1px solid var(--purple-color);
  padding-bottom: 2px;
  max-width: fit-content;
}

@media (max-width: 425px) {
  .about-left {
    padding: 20px;
    text-align: left;
    max-width: 100%;
  }
}
